.landing {
    background-image: url('../bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-clip: cover;
    background-size: cover;
    background-attachment: fixed;

    text-align: center;

    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.logo {
    margin-top: 125px;
    width: 25%;
    height: auto;
    -ms-flex-item-align: center;
        align-self: center;
}

.tagline {
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    font-size: 25px;
    color: white;
    line-height: 2rem;
}

.tabBar {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
}

.tabBar .button {
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 25px;
    padding-bottom: 25px;

    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    font-size: 25px;
    color: white;
    border: none;
}

@media only screen and (max-width: 450px) {
    .logo {
        width: 60%;
    }
    .tagline {
        text-align: left;
        line-height: 4rem;
        margin-right: 50%;
        margin-left: 10%;
    }
    .tabBar .button {
        font-size: 23px;
    }
}




