.circle {
    width: 100px;
    height: 100px;
    background-color: #EF4A30;
    border-radius: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    position: fixed;
    bottom: 25px;
    right: 25px;
    border: none;
}

.circle h1 {
    font-family: 'Raleway', sans-serif;
    font-weight: 500;
    font-size: 70px;
    color: white;
}

@media only screen and (max-width: 450px) {

    .circle {
        width: 75px;
        height: 75px;
    }

    .circle h1 {
        font-size: 50px;
    }

}


